import GmAlert from '../../component/alert'
import GmInformation from '../../component/information/Information'
import type { MsgType } from '../../component/message'
import GmMessage from '../../component/message'
import GmNotice from '../../component/notice'
import { getProgress, setMsgCount, setProgress } from '../../utils/html'

export interface OneMsg extends Omit<MsgType, 'open'> {
  // 用于标识消息是否重复, 这是内容+类型字符串的组合
  // 一般只用于messsage和notice
  readonly identifer: string
  timer?: NodeJS.Timeout
  count: number
}

export interface Config {
  timeout: number
  maxCount: number
}

export interface MsgPropsExt {
  timeout?: number
  text?: string
  headerLeft?: string
  headerRight?: string
  showClose?: boolean
  onClosed?: (status: number) => void
  showConfirm?: boolean
  showCancel?: boolean
}

/**
 * 消息容器
 */
export class Msg {
  timeout = 2500

  maxCount = 8

  private activeInsts: Map<string, OneMsg> = new Map()

  // 0:'msg' | 1:'notice' | 2:'alert' | 3:'information'
  form: number

  constructor(form: number) {
    this.form = form

    if (form > 1) {
      this.timeout = 0
    }
  }

  config(config: Partial<Config>) {
    this.timeout = config.timeout || this.timeout
    this.maxCount = config.maxCount || this.maxCount
  }

  fire(
    text: string,
    type?: 'success' | 'error' | 'warning' | 'info' | 'loading',
    conf?: MsgPropsExt,
  ) {
    const oMsg = this.judgeReMsg(text, type || 'success', conf)
    if (type !== 'loading') {
      this.setTimeOut(oMsg, conf?.timeout || this.timeout)
    }

    return oMsg
  }

  // 设置定时
  private setTimeOut(oMsg: OneMsg, timeout?: number) {
    if (!timeout) return
    const $el = oMsg.$el

    setProgress($el, 1, timeout)

    oMsg.timer ??= setInterval(() => {
      if (getProgress($el) === 0) {
        oMsg.close(-1)
        clearInterval(oMsg.timer)
      }
    }, 150)

    $el.addEventListener('mouseenter', () => {
      setProgress($el, getProgress($el), timeout, true)
    })

    $el.addEventListener('mouseleave', () => {
      setProgress($el, getProgress($el), timeout)
    })
  }

  // 判断消息是否存在, 设置msgCount以及关闭多余消息
  private judgeReMsg(
    content: string,
    type: 'success' | 'error' | 'warning' | 'info' | 'loading',
    conf?: MsgPropsExt,
  ) {
    const id = `${content}${type}`
    if (this.form < 2) {
      for (const inst of this.activeInsts) {
        if (inst[1].identifer === id) {
          inst[1].count += 1
          setMsgCount(inst[1].$el, inst[1].count)
          return inst[1]
        }
      }
    }

    const props = {
      ...conf,
      content,
      type,
      onClosed: (status: number) => {
        this.form < 2 && this.activeInsts.delete(id)
        conf?.onClosed && conf.onClosed(status)
      },
    }
    let inst: MsgType

    switch (this.form) {
      case 0:
        inst = GmMessage(props)
        break
      case 1:
        inst = GmNotice(props)
        break
      case 2:
        inst = GmAlert(props)
        break
      case 3:
        inst = GmInformation(props)
        break
      default:
        inst = GmMessage(props)
        break
    }

    if (this.form < 2) {
      this.activeInsts.size >= this.maxCount &&
        this.activeInsts.values().next().value.close(-2)
    } else {
      // 关闭所有消息
      this.activeInsts.values().next().value?.close(-2)
      this.activeInsts.clear()
    }
    const oMsg: OneMsg = { ...inst, identifer: id, count: 1 }

    this.activeInsts.set(id, oMsg)
    inst.open()

    return oMsg
  }
}
